.App {
  text-align: center;
}
:root input {
  width: 95%;
}
:root select {
  width: 90%;
  padding: auto;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
h2 {
  margin: 30px 0;
  font-weight: bold;
  font-size: xx-large;
}

.currency {
  padding: 15px;
  gap: 10px;
  max-width: 70%;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.297);
  font-weight: bolder;
}
form div {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
